import React from 'react';
import "./signup.css";
import { Link, useNavigate } from 'react-router-dom';
import { useState } from 'react';

import axios from 'axios';
const Login = () => {
    const [emailOrUserName, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState(null);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const navigate = useNavigate()

    const handleSubmit = async (event) => {
        event.preventDefault();
        setIsSubmitting(true);

        if (!emailOrUserName && !password) {
            setErrorMessage("      ");
            setIsSubmitting(false);

            return;
        }
        else if (!emailOrUserName) {
            setErrorMessage("    ");
            setIsSubmitting(false);
            return;

        }
        else if (!password) {
            setErrorMessage("     ");
            setIsSubmitting(false);
            return;

        }
        else {
            try {
                const formData = {
                    emailOrUserName,
                    password,
                    loginProvider: 0,
                };
                const response = await axios.post("https://deepcoding-001-site1.itempurl.com/API/eCommerce/Identity/User/Authenticate", formData);

                setErrorMessage(response?.data?.message || "");

                if (response.data.isSuccess) {
                    localStorage.setItem('token', response.data.result.value);
                    navigate('/home');
                }
            } catch (error) {
                setErrorMessage(error.response?.data?.message || "   ");
            } finally {
                setIsSubmitting(false);
            }

        }



    };


    return (
        <div className='containerd ' dir="rtl">
            <div className='right'>

                <div className='logo'>
                    <img src={require("./Image/logo.png")} alt='logo' />
                </div>

                <div className='Info'>
                    <div className='Info_text'>
                        <h2>  </h2>
                        <h4>    <Link to="sign" className='Link'>     </Link> </h4>
                    </div>
                </div>
                <div className='form_input' >
                    <form onSubmit={handleSubmit} >
                        {errorMessage && (
                            <div className={errorMessage.includes('successful') ? "alert alert-success mt-3" : "alert alert-danger mt-3"} role="alert">
                                {errorMessage}
                            </div>
                        )}

                        <div className="form-floating mb-3">
                            <input type="text" className="form-control " id="floatingInput" placeholder=" " value={emailOrUserName}
                                onChange={(event) => setUsername(event.target.value)} />
                            <label htmlFor="floatingInput" >      </label>
                        </div>

                        <div className="form-floating mb-3">
                            <input
                                type="password"
                                className="form-control"
                                id="floatingInput"
                                placeholder=" "
                                value={password}
                                onChange={(event) => setPassword(event.target.value)}
                            />
                            <label htmlFor="floatingInput"> </label>
                        </div>


                        <div className="d-grid gap-2">
                            <button
                                className="btn btnSign btn-primary btn-lg text-center" type="submit"
                                disabled={isSubmitting}
                            >
                                {isSubmitting ? ' ...' : '    '}
                            </button>
                        </div>




                    </form>
                </div>

            </div>
            <div className='left'>
                <div className='cardd'>
                    <h2>   </h2>
                    <p>design__abary</p>
                    <img src={require("./Image/undraw.png")} alt='undraw' />
                </div>


            </div>


        </div>
    )
}

export default Login