import React from 'react'
import { LiaLessThanSolid } from "react-icons/lia";
import Dropdown from 'react-bootstrap/Dropdown';
import { Link } from 'react-router-dom';
import { CiLogout } from "react-icons/ci";
import { IoIosLock } from "react-icons/io";
import axios from 'axios';
import './header.css';

function Header(props) {
    const token = localStorage.getItem("token")
    const Logout = async () => {
        try {
            await axios.get('https://deepcoding-001-site1.itempurl.com/API/eCommerce/Identity/User/Logout',
                {
                    headers: {
                        'accept': 'application/json',
                        "Authorization": `Bearer ${token} `
                    }
                }
            );
            localStorage.removeItem('token');

        } catch (error) {
            console.error('Logout failed:', error.message);
        }

    }

    return (
        <div className='header'>
            <div className='info'>
                <h2> <span></span> <LiaLessThanSolid className='Icon' />     <LiaLessThanSolid className='Icon' /> {props.name}</h2>
            </div>
            <Dropdown>
                <Dropdown.Toggle id="dropdown-basic" >
                    <span className='ms-3'>  </span>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                    <Dropdown.Item className='link' ></Dropdown.Item>
                    <Dropdown.Item  >
                        <Link className='link' to="/" onClick={Logout}>
                            <span>  </span>
                            <CiLogout className='Icon' />
                        </Link>
                    </Dropdown.Item>
                    <Dropdown.Item  >
                        <Link className='link' to="/home/changepass">
                            <span>    </span>
                            <IoIosLock className='Icon' /></Link>
                    </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>

        </div>
    )
}

export default Header