import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import "./changepassword.css";

const ChangePass = () => {
    const [passwordOld, setPasswordOld] = useState();
    const [passwordNew, setPasswordNew] = useState();
    const [passwordConNew, setPasswordconNew] = useState();
    const [errorMessage, setErrorMessage] = useState(null);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const token = localStorage.getItem("token");
    console.log(passwordConNew, passwordNew, passwordOld)
    const navigate = useNavigate();

    const handleRegister = async (e) => {
        e.preventDefault();
        setIsSubmitting(true);
        setErrorMessage('');

        if (!passwordOld || !passwordNew || !passwordConNew) {
            setErrorMessage(' ');
            setIsSubmitting(false);
            return;
        }

        if (passwordNew !== passwordConNew) {
            setErrorMessage('   ');
            setIsSubmitting(false);
            return;
        }

        const payload = {
            oldPassword: passwordOld,
            newPassword: passwordNew,
            confirmNewPassword: passwordConNew
        };

        try {
            const response = await axios.put(
                'https://deepcoding-001-site1.itempurl.com/API/eCommerce/Identity/User/ChangePassword',
                payload,
                {
                    headers: {
                        'accept': 'application/json',
                        "Authorization": `Bearer ${token} `,
                        'Content-Type': 'application/json'
                    },
                }
            );
            navigate('/');
            console.log(response)
        } catch (error) {

            setErrorMessage(
                error.response?.data?.message || '   '
            );
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className='ChangePassword'>
            <div className='heading'>
                <h3>  </h3>
            </div>
            <form onSubmit={handleRegister}>
                {errorMessage && (
                    <div className={`alert ${errorMessage.includes('successful') ? 'alert-success' : 'alert-danger'} mt-3`} role="alert">
                        {errorMessage}
                    </div>
                )}
                <div className="form-floating mb-3">
                    <input
                        type="text"
                        className="form-control"
                        id="floatingInput"
                        placeholder="  "
                        value={passwordOld}
                        onChange={(event) => setPasswordOld(event.target.value)}
                    />
                    <label htmlFor="floatingInput">  </label>
                </div>
                <div className="form-floating mb-3">
                    <input
                        type="text"
                        className="form-control"
                        id="floatingInput"
                        placeholder="  "
                        value={passwordNew}
                        onChange={(event) => setPasswordNew(event.target.value)}
                    />
                    <label htmlFor="floatingInput">  </label>
                </div>
                <div className="form-floating mb-3">
                    <input
                        type="text"
                        className="form-control"
                        id="floatingInput"
                        placeholder="  "
                        value={passwordConNew}
                        onChange={(event) => setPasswordconNew(event.target.value)}
                    />
                    <label htmlFor="floatingInput">   </label>
                </div>



                <div className="d-grid gap-2">
                    <button
                        className="btn btnSign btn-primary btn-lg text-center"
                        type="submit"
                        disabled={isSubmitting}
                    >
                        {isSubmitting ? ' ...' : '    '}
                    </button>
                </div>
            </form>
        </div>
    );
};



export default ChangePass;
